body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container {
  display: flex;
  flex-direction: column;
}

.showcase-container {
  position: relative;
}

.showcase {
  font-family: "Open Sans", sans-serif;
  height: 100vh;
  background-color: rgba(48, 83, 117, 0.8);
  position: absolute;
  top: 0px;
  left: 0px;
  color: white;
  width: 100%;
}

.showcase:before {
  content: "";
  background: url(/static/media/Home-Background-Image.8784cb96.jpg) no-repeat center
    center/cover;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: -1;
}

.showcase .showcaseInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  height: 100%;
}

.showcase h1 {
  font-size: 3.5rem;
  font-weight: 500;
  margin: 0px;
}

.description {
  width: 60%;
  font-size: 17px;
  font-weight: 550;
  color: rgb(237, 248, 253);
}

.projectsBtn {
  height: 30px;
  width: 120px;
  background-color: rgb(239, 247, 255);

  border: none;
  border-radius: 15px;
  font-size: 15px;
  font-weight: 500;
}

.projectsBtn:hover {
  cursor: pointer;
  background-color: rgb(255, 255, 255);
}

.home-btn-container {
  margin: 10px;
}

.home-btn-container > a {
  padding: 5px 10px;
  text-decoration: none;
  color: rgb(247, 247, 247);
  border: 2px solid rgb(247, 247, 247);
  border-radius: 10px;
}

.down-arrow {
  margin-top: 120px;
  width: 50px;
  height: 50px;
  -webkit-animation: downSignal 0.8s infinite 2s linear;
          animation: downSignal 0.8s infinite 2s linear;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}

.down-arrow:hover {
  cursor: pointer;
}

@-webkit-keyframes downSignal {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  to {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}

@keyframes downSignal {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  to {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}

@media (max-width: 780px) {
  .showcase h1 {
    font-size: 3rem;
  }

  .description {
    font-size: 1rem;
  }
}

@media (max-width: 600px) {
  .showcase h1 {
    font-size: 2rem;
  }

  .description {
    font-size: 0.7rem;
  }
}

.about {
  padding-top: 60px;
  margin-bottom: 60px;
  background-color: rgb(241, 241, 241);
  /* background-color: rgb(248, 248, 248); */
}

.about-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans", sans-serif;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.about-container::after {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  background-color: rgb(156, 156, 156);
}

.about-center {
  display: flex;
  height: 500px;
  width: 80%;
}

.about-text {
  margin-top: 50px;
  position: absolute;
  left: 50vw;
  width: 1235px;
  height: 300px;

  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 200px;
  line-height: 169px;
  display: flex;
  align-items: center;
  text-align: center;

  color: rgba(52, 109, 163, 0.8);

  opacity: 0.7;
  -webkit-transform: rotate(-7.5deg);
          transform: rotate(-7.5deg);
}

.about-info-container {
  position: relative;
  width: 500px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-info-container::after {
  content: "";
  width: 500px;
  height: 500px;
  position: absolute;
  top: 0;
  left: 0;
  background: #e0b9b9;
  opacity: 0.71;
  /* border-radius: 10px;  */
}

.about-info-1,
.about-info-2 {
  font-family: "Varela Round", sans-serif;
  text-align: justify;
  font-weight: 800;
  font-size: 1.1rem;
  color: #1e3c69;
  line-height: 120%;
  z-index: 1;
  letter-spacing: 1px;
}

.about-info-1 {
  width: 340px;
}

.about-info-2 {
  width: 340px;
}

.fade-in {
  opacity: 1;
  -webkit-animation-name: fadeInOpacity;
          animation-name: fadeInOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

@-webkit-keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInOpacity {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

