.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.project-container {
  width: 80%;
  margin-top: 60px;
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: repeat(auto-fill, 300px);
  grid-column-gap: 40px;
  grid-row-gap: 40px;
  justify-content: center;
}

.projectTitle {
  width: 100%;
  height: 80px;
  font-size: 50px;
  font-weight: 800;
  color: #254a68;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.fade-in-project {
  opacity: 1;
  -webkit-animation-name: fadeInOpacityProjects;
          animation-name: fadeInOpacityProjects;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

@-webkit-keyframes fadeInOpacityProjects {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInOpacityProjects {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 800px) {
  .project-container {
    grid-template-columns: repeat(auto-fill, 500px);
    margin-left: 0px;
    justify-content: center;
    justify-self: center;
  }
}

@media (max-width: 420px) {
  .project-container {
    grid-template-columns: repeat(auto-fill, 350px);
    justify-content: center;
  }
}

