.contact-container {
  margin-top: 50px;
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #376f9c;
}

.contact-title {
  height: 100px;
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 50px;
  line-height: 80px;
  display: flex;
  align-items: center;
  text-align: center;
  color: white;
}

.contact-content {
  width: 500px;
  align-items: stretch;
  height: 500px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.contact-btn {
  background-color: whitesmoke;
  width: 100px;
  height: 30px;
  border-radius: 15px;
  border-style: none;
  color: #2e6394;
  font-family: Roboto;
  font-size: 17px;
  font-weight: 600;
}

.contact-btn:hover {
  cursor: pointer;
  background-color: rgb(253, 177, 127);
}

.email-btn {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.email-btn a {
  color: inherit;
  margin: 0px;
}

@media (max-width: 500px) {
  .contact-container {
    height: 300px;
    margin-top: 20px;
    padding-top: 20px;
  }
  .contact-content {
    flex-direction: column;
  }
  .contact-btn {
    margin-top: 10px;
  }
}
